<template>
    <div>
        <ul class="collection with-header">
            <li class="collection-header"><h4>Mr.{{name}}</h4></li>
            <li class="collection-item"><div>DEPARTMENT : {{depart}}</div></li>
            <li class="collection-item"><div>POSITION : {{position}}</div></li>
        </ul>
        <a class="waves-effect waves-light btn gray" @click="$router.push('/')">back</a>
        <a class="waves-effect waves-light btn orange" @click="$router.push('/edit/'+ employee_id)">Edit</a>
        <a class="waves-effect waves-light btn red" v-on:click="deleteUser()">delete</a>
    </div>
      
</template>
<script>
import db from './firebaseinit'
export default {
    name:'view-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            depart:null,
            position:null
        }
    },

    created(){
        let userId = this.$route.params.employee_id
        db.collection('employee').where('employee_id', '==', userId).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                this.employee_id = doc.data().employee_id,
                this.name=doc.data().name,
                this.depart=doc.data().depart,
                this.position=doc.data().position
            })
        }
        )
    },

    methods:{
        deleteUser(){
            let con = confirm("You want to delete this employee")
            if(con){
                db.collection('employee').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
                 querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
             }
            )
            }
            
        }
    }
}
</script>
<style scoped>

</style>


