<template>
    <div>
        <div class="row">
    <form v-on:submit.prevent="editData" class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input id="password" type="text" class="validate" v-model="employee_id" disabled>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input id="password" type="text" class="validate" v-model="name">
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input id="password" type="text" class="validate" v-model="depart">
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input id="password" type="text" class="validate" v-model="position">
        </div>
      </div>
      <div class="row">
          <div class="input-field col s12">
              <button class="btn waves-effect waves-light orange" type="submit" name="action">Edit
              </button>
          </div>
      </div>
    </form>
  </div>
    </div>
</template>
<script>
import db from './firebaseinit'
export default {
    name:'edit-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            depart:null,
            position:null
        }
    },
    created(){
        let userId = this.$route.params.employee_id
        db.collection('employee').where('employee_id', '==', userId).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                this.employee_id = doc.data().employee_id,
                this.name=doc.data().name,
                this.depart=doc.data().depart,
                this.position=doc.data().position
            })
        }
        )
    },

    methods:{
        editData(){
            db.collection('employee').where('employee_id','==',this.$route.params.employee_id).get().then(queryshot=>{
                    queryshot.forEach(doc => {
                        doc.ref.update({
                            employee_id: this.employee_id,
                            name: this.name,
                            depart: this.depart,
                            position: this.position
                        })
                        this.$router.push({name:'ViewEmployee',params:this.$route.params.employee_id})
                    })
                }
            )
        }
    }

}
</script>
<style scoped>

</style>


