<template>
    <div>
        <div class="row">
    <form v-on:submit.prevent="addData" class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input id="password" type="text" class="validate" v-model="employee_id">
          <label for="password">Employee ID</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input id="password" type="text" class="validate" v-model="name">
          <label for="password">Name and LastName</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input id="password" type="text" class="validate" v-model="depart">
          <label for="password">Department</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input id="password" type="text" class="validate" v-model="position">
          <label for="password">Position</label>
        </div>
      </div>
      <div class="row">
          <div class="input-field col s12">
              <button class="btn waves-effect waves-light" type="submit" name="action">Add employee
              </button>
          </div>
      </div>
    </form>
  </div>
    </div>
</template>
<script>
import db from './firebaseinit'
export default {
    name:'new-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            depart:null,
            position:null
        }
    },

    methods:{
        addData(){
            db.collection('employee').add({
                employee_id: this.employee_id,
                name: this.name,
                depart: this.depart,
                position: this.position
            }).then(doc=>{
                this.$router.push('/')
            })
        }
    }
}
</script>
<style scoped>

</style>


