<template>
    <div>
        <ul class="collection with-header">
            <li class="collection-header"><h4>First Names</h4></li>
            <li v-for="data in employees" v-bind:key="data.employee_id" class="collection-item">
                <div class="chip">{{data.employee_id}}</div>{{data.name}} : {{data.department}} 
                <a class="secondary-content waves-effect waves-light btn-small" v-on:click="navigateTo('/view/'+data.employee_id)">
                View</a>
            </li>
      </ul>
        <div class="fixed-action-btn">
            <div class="btn-floating btn-large red">
               <router-link to="/new"><i class="fa fa-plus"></i></router-link> 
            </div>
        </div>
    </div>
</template>
<script>
import db from './firebaseinit'
export default {
    name:'dashboard',
    data(){
        return{
            employees:[]
        }
    },

    created(){
        db.collection('employee').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                let data = {
                    'id': doc.id,
                    'employee_id':doc.data().employee_id,
                    'name':doc.data().name,
                    'department':doc.data().depart,
                    'position':doc.data().position
                }
                this.employees.push(data)
            })
            console.log(this.employees)
            this.employees.reverse()
        }
        )
    },

   methods: {
       navigateTo(route){
           this.$router.push(route)
       }
   }
}
</script>
<style scoped>

</style>


